import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { listWeather } from '../app/store/actions/weather'
import { connect } from 'react-redux'
import { Table, TableBody, TableCell, TableHead, TableRow, Paper } from '@material-ui/core';

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      weatherResponse: [],
      city: 'Jakarta',
      error: '',
      initialLoading: true,
    }
  }

  async componentDidMount() {
    this.getWeather()
  }

  async getWeather() {
    try {
      const listWeathers = await listWeather(this.state.city)
      if (listWeathers.status !== 200) throw Error()
      this.setState({ weatherResponse: listWeathers })
    } catch (e) {
      this.setState({ error: 'Terjadi kesalahan pada saat memuat data' })
    }
    this.setState({ initialLoading: false })
  }

  render() {
    console.log(this.state.weatherResponse)
    return (
      <Paper>
        <p>test</p>
      </Paper>
    )
  }
}

const mapStateToProps = (state) => ({
  user: state.city,
})

export default connect(mapStateToProps)(App)
